import React from 'react'
import '../SignIn/SignIn'

export default function Register({ onRegisterButtonPressed }) {
    const userData = { name: '', username: '', email: '', password: '' }
    const onUsernameChange = (event) => userData.username = userData.name = event.target.value;

    const onEmailChange = (event) => userData.email = event.target.value;
    const onPasswordChange = (event) => userData.password = event.target.value;
    const button = () => onRegisterButtonPressed(userData);


    return (
        <div className="center fullscreen" style={{ background: '#9eebcf' }}>
            <article className="pa4 black-80 " style={{
                borderRadius: '50px',
                background: 'linear-gradient(145deg, #a9fbdd, #8ed4ba)',
                boxShadow: '20px 20px 60px #86c8b0 -20px -20px 60px #b6ffee',
                zIndex: '100'

            }} >
                <fieldset id="sign_up" className="dtc tc text-center ba b--transparent ph0 mh0">
                    <h1>Register</h1>
                    <legend className="ph0 mh0 fw6 clip">Sign Up</legend>
                    <div className="mt3">
                        <label className="db fw4 lh-copy f6" htmlFor="email-address">username</label>
                        <input onChange={onUsernameChange} className="pa2 input-reset ba bg-transparent w-100 measure br2 white bw1" type="text" name="username" id="username" />
                    </div>
                    <div className="mt3">
                        <label className="db fw4 lh-copy f6" htmlFor="email-address">email</label>
                        <input onChange={onEmailChange} className="pa2 input-reset ba bg-transparent w-100 measure br2 white bw1" type="email" name="email" id="email" />
                    </div>
                    <div className="mt3">
                        <label className="db fw4 lh-copy f6" htmlFor="password">Password</label>
                        <input onChange={onPasswordChange} className="b pa2 input-reset ba bg-transparent br2 white bw1" type="password" name="password" id="password" />
                    </div>
                    <div className="mt3"><button onClick={button} className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 br2 "  >Register</button></div>
                </fieldset>
            </article>

        </div>
    )
}
