import React from 'react'
import '../Home/Home.css'

export default function Home({ users, onDelete, onHome }) {

    return (
        <div className="fullscreen flexcenter bg-red ">
            <div className="pa4 ">
                <div className="overflow-auto" style={{ zIndex: '100' }}>
                    <table className="ba b--white bw2 br4 f6 w-100 mw8" cellSpacing="0">
                        <thead>
                            <tr className="stripe-dark">
                                <th className="fw6 tl pa3 bg-white">No.</th>
                                <th className="fw6 tl pa3 bg-white">Username</th>
                                <th className="fw6 tl pa3 bg-white">Email</th>
                                <th className="fw6 tl pa3 bg-white">Action</th>
                            </tr>
                        </thead>
                        <tbody className="lh-copy white">

                            {users.map(user => {
                                if (user !== null) return <tr key={user.id} className="stripe-dark">
                                    <td className="pa3 id">{user.id}</td>
                                    <td className="pa3">{user.username}</td>
                                    <td className="pa3">{user.email}</td>
                                    <td className="pa3">
                                        <button id={user.id} key={user.id} onClick={function (event) { onDelete(event.target.id); onHome(); }} className="bg-red f6 grow br-pill ba bw2 ph3 pv2 mb2 dib white" >
                                            Delete
                                        </button>
                                    </td>
                                </tr>
                            })}

                        </tbody>
                    </table>
                </div>
            </div>

        </div>

    )
}
