import React from 'react'
import './SignIn.css'

export default function SignIn({ onSignInButtonPressed, onSignInUsernameChange, onSignInPasswordChange }) {
    const onUsernameChange = (event) => {
        onSignInUsernameChange(event.target.value)
    }
    const onPasswordChange = (event) => {
        onSignInPasswordChange(event.target.value)
    }
    return (
        <div className="center fullscreen">
            <article className="stack pa4 black-80 neumorphism">
                <fieldset id="sign_up" className="dtc tc text-center ba b--transparent ph0 mh0">
                    <h1 >Sign In</h1>
                    <legend className="ph0 mh0 fw6 clip">Sign Up</legend>
                    <div className="mt3">
                        <label className="db fw4 lh-copy f6" htmlFor="email-address">username</label>
                        <input onChange={onUsernameChange} className="pa2 input-reset ba white br2  bg-transparent w-100 measure" type="text" name="username" id="username" />
                    </div>
                    <div className="mt3">
                        <label className="db fw4 lh-copy f6" htmlFor="password">Password</label>
                        <input onChange={onPasswordChange} className="b pa2 input-reset ba white br2 bg-transparent" type="password" name="password" id="password" />
                    </div>
                    <div className="mt3">
                        <button onClick={onSignInButtonPressed} className="b br2 ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6"  >Sign In</button>
                    </div>
                </fieldset>
            </article>

        </div>
    )
}
