import React from 'react'
import './Navbar.css'

export default function Navbar({ route, onRouteChange }) {
    return (
        <div className=" navbar pt3 white tr pr5 v-btm "  >

            {route === 'signin'
                ? <p onClick={onRouteChange} className="dib underline grow mr4 f3 pointer">Register</p>
                : route === 'register'
                    ? <p onClick={onRouteChange} className="dib underline grow mr4 f3 pointer">Sign In</p>
                    : <p onClick={onRouteChange} className="dib underline grow mr4 f3 pointer">Logout</p>
            }

        </div>
    )
}
