import React, { Component } from 'react';
import './App.css';
import 'tachyons'
import SignIn from './components/SignIn/SignIn'
import Register from './components/Register/Register'
import Home from './components/Home/Home'
import Particles from 'react-particles-js';
import Navbar from './components/Navbar/Navbar'

const url = 'https://fierce-anchorage-83457.herokuapp.com/';
class App extends Component {
  constructor() {
    super()
    this.state = {
      route: 'signin',
      navbarColor: 'bgblue',
      signinUsername: '',
      signinPass: '',
      users: [{ name: "name", username: "username", id: 90, email: "@gmai.com" }]
    }
  }


  onRouteChange = async () => {
    const tmp = this.state.route === 'signin'
      ? 'register'
      : this.state.route === 'register'
        ? 'signin'
        : this.state.route === 'home' ? 'signin' : 'home';
    await this.setState({
      route: tmp
    })

    if (tmp === 'signin') {
      await this.setState({
        signinPass: '', signinUsername: ''
      })
    }
  }

  onSignInButtonPressed = () => {
    const { signinUsername, signinPass } = this.state

    fetch(url + 'signin/', {
      headers: { 'Content-Type': 'application/json' },
      method: 'post',
      body: JSON.stringify({
        username: signinUsername,
        password: signinPass
      })
    })
      .then(res => res.json())
      .then(response => {
        if (response.username) {
          this.onHome()
          return this.setState({
            route: 'home'
          })
        }
      })
      .catch(err => console.log("failed to signin"))


  }

  onSignInUsernameChange = async (username) => {
    await this.setState({
      signinUsername: username
    })
  }
  onSignInPasswordChange = async (password) => {
    await this.setState({
      signinPass: password
    })
  }

  onRegisterButtonPressed = (dataUser) => {
    fetch(url + 'register/', {
      headers: { 'Content-Type': 'application/json' },
      method: 'post',
      body: JSON.stringify({
        username: dataUser.username,
        email: dataUser.email,
        password: dataUser.password
      })
    })
      .then(res => res.json())
      .then(response => {
        console.log(response);
        if (response.status === 'success') {
          return this.setState({
            route: 'signin'
          })
        }
        return console.log("gagal register1");
      })
      .catch(err => console.log("gagal register2"))

  }

  onHome = async () => {
    await fetch(url + 'users/')
      .then(res => res.json())
      .then(response => {
        this.setState({
          users: response
        })
      })

  }

  onDelete = (id) => {
    fetch(url + 'delete/' + id, {
      headers: { 'Content-Type': 'application/json' },
      method: 'delete',
      body: JSON.stringify({ id: id })
    })
      .then(res => res.json)
      .then(resp => {
        if (resp.status === 'success') {
          this.onHome()
        }
      })
      .catch(err => console.log("error while deleting"))
  }

  deleteAUser = () => {
    this.onDelete(0);
  }

  render() {
    const { route, navbarColor } = this.state
    const particlesParam = {
      particles: {
        number: {
          value: 150,
          density: {
            enable: true,
            value_area: 800
          }
        }
      }
    }

    return (
      <div className={navbarColor}>
        <Particles className="particles" params={particlesParam} />
        <Navbar onRouteChange={this.onRouteChange} color={navbarColor} route={route} />
        {route === 'signin'
          ? <SignIn
            onSignInButtonPressed={this.onSignInButtonPressed}
            onSignInPasswordChange={this.onSignInPasswordChange}
            onSignInUsernameChange={this.onSignInUsernameChange} />
          : route === 'register'
            ? <Register
              onRegisterButtonPressed={this.onRegisterButtonPressed} />
            : <Home onHome={this.onHome} onDelete={this.onDelete} users={this.state.users} />}
      </div>
    );
  }
}

export default App;
